
.receiveAddress {
  background: url("@/assets/lottery/receive_address_bg.png") no-repeat;
  background-size: 100% 100%;
  min-height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      justify-content: center;
      margin: 39px 0 0;
      .information {
        width: 92%;
        display: flex;
        flex-direction: column;
        align-items: center;
        display: flex;
        background: rgba(106, 6, 160, 0.9);
        border: 1px solid #ddb6f4;
        border-radius: 16px;
        .title {
          font-size: 18px;
          font-family: PingFang SC-中等, PingFang SC;
          font-weight: normal;
          color: #ffffff;
          line-height: 14px;
          margin: 20px 0 24px;
        }
        .user_form {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          .user_name,
          .user_phone,
          .user_site {
            width: 90%;
            display: flex;
            flex-direction: column;
            margin: 0 0 15px;
            .label {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ffffff;
              line-height: 14px;
              margin: 0 0 10px;
            }
          }
          .save {
            width: 1.63rem;
            height: 0.48rem;
            background: linear-gradient(180deg, #ff7358 0%, #ff2a00 100%);
            font-size: 16px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #ffffff;
            line-height: 16px;
            border-color: #ff7358;
            border-radius: 30px;
            margin: 10px 0 20px;
          }
        }
      }
    }
  }
  .introduceBox {
    display: flex;
    justify-content: center;
    .introduce {
      width: 88%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .caption {
        width: 100%;
        display: flex;
        margin: 30px 0 10px;
        .title {
          font-size: 14px;
          font-family: PingFang SC-粗体, PingFang SC;
          font-weight: normal;
          color: #ffffff;
          line-height: 14px;
        }
      }
      .container {
        width: 100%;
        display: flex;
        flex-direction: column;
        .message {
          font-size: 14px;
          font-family: PingFang SC-中等, PingFang SC;
          font-weight: normal;
          color: #ffffff;
          line-height: 26px;
        }
      }
    }
  }
}
.van-field {
  border-radius: 8px;
  background: rgba(182, 131, 207, 0.9);
}
::v-deep .van-field__control {
  font-size: 15px;
  font-family: PingFang SC-中等, PingFang SC;
  font-weight: normal;
  color: #fefffe;
  line-height: 14px;
}
::v-deep .van-field__control::-webkit-input-placeholder {
  font-size: 14px;
  font-family: PingFang SC-中等, PingFang SC;
  font-weight: normal;
  color: #dfb2ff;
  line-height: 14px;
}
.address {
  border-radius: 8px 8px 0 0;
}
.street {
  border-radius: 0 0 8px 8px;
}
